    .education {
       font-family: courier new, courier, monospace;
       font-style: normal;
       padding-top: 20px;
       padding-left: 20px;
       padding-right: 20px;

        @media screen and (min-width: 1200px) {
           padding-top: 40px;
           padding-left: 40px;
           padding-right: 40px;

        }
    }

   .education__title {
       font-weight: bold;
       color: var(--bg-title-text-color);

       @media screen and (min-width: 480px) {
           font-size: 20px;
           line-height: 25px;
       }

       @media screen and (min-width: 768px) {
           font-size: 15px;
           line-height: 25px;
       }

       @media screen and (min-width: 1200px) {
           font-size: 24px;
           line-height: 25px;
       }
   }

   .education__title--current {
       @media screen and (min-width: 480px) {
           margin-top: 40px;
       }

       @media screen and (min-width: 768px) {
           margin-top: 24px;
       }

       @media screen and (min-width: 1200px) {
           margin-top: 60px;
       }
   }

