.skills {
    font-family: courier new, courier, monospace;;
    font-style: normal;
    color: var(--accent-color);
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 480px) {
        padding-top: 20px;
        padding-left: 20px;
    }

    @media screen and (min-width: 768px) {
        padding-top: 20px;
        padding-left: 20px;
    }

    @media screen and (min-width: 1200px) {
        padding-top: 20px;
        padding-left: 20px;
    }
}

.skills__title {
    font-weight: bold;

    @media screen and (min-width: 480px) {
        font-size: 20px;
        line-height: 25px;
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
        line-height: 15px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 24px;
        line-height: 25px;
    }
}

.skills__list {
    font-weight: 500;
    margin-top: 10px;

    @media screen and (min-width: 480px) {
        margin-top: 10px;
        font-size: 15px;
        line-height: 17px;
    }

    @media screen and (min-width: 768px) {
        margin-top: 17px;
        font-size: 12px;
        line-height: 15px;
    }

    @media screen and (min-width: 1200px) {
        margin-top: 35px;
        font-size: 19px;
        line-height: 25px;
    }
}